import { supabase } from "./supabase"

/**
 *    
 *        .
 */
export const trackPostVisit = async postSlug => {
  if (!supabase || !postSlug) {
    return
  }

  try {
    const now = new Date().toISOString()

    //     
    const { data: existingPost, error: fetchError } = await supabase
      .from("post_visits")
      .select("*")
      .eq("post_slug", postSlug)
      .maybeSingle()

    if (fetchError) {
      throw fetchError
    }

    if (existingPost) {
      //           
      const { error: updateError } = await supabase
        .from("post_visits")
        .update({
          visit_count: existingPost.visit_count + 1,
          last_visited_at: now,
        })
        .eq("post_slug", postSlug)

      if (updateError) throw updateError
    } else {
      //   
      const { error: insertError } = await supabase.from("post_visits").insert([
        {
          post_slug: postSlug,
          visit_count: 1,
          last_visited_at: now,
        },
      ])

      if (insertError) throw insertError
    }
  } catch (error) {
    console.error("Error tracking post visit:", error)
  }
}

/**
 *     
 */
export const getPostVisitInfo = async postSlug => {
  if (!supabase || !postSlug) {
    return { visitCount: 0, lastVisitedAt: null }
  }

  try {
    const { data, error } = await supabase
      .from("post_visits")
      .select("visit_count, last_visited_at")
      .eq("post_slug", postSlug)
      .maybeSingle()

    if (error) {
      throw error
    }

    //   
    if (!data) {
      return { visitCount: 0, lastVisitedAt: null }
    }

    return {
      visitCount: data?.visit_count || 0,
      lastVisitedAt: data?.last_visited_at || null,
    }
  } catch (error) {
    console.error("Error fetching post visit info:", error)
    return { visitCount: 0, lastVisitedAt: null }
  }
}

/**
 *      
 */
export const getTopVisitedPosts = async (limit = 3) => {
  if (!supabase) {
    return []
  }

  try {
    const { data, error } = await supabase
      .from("post_visits")
      .select("post_slug, visit_count, last_visited_at")
      .order("visit_count", { ascending: false })
      .limit(limit)

    if (error) {
      throw error
    }

    return data || []
  } catch (error) {
    console.error("Error fetching top visited posts:", error)
    return []
  }
}
